@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:ital,wght@0,400;0,500;0,600;0,700;1,800&display=swap);
body,html{height:100%;width:800vw;margin:0;background-color:#f5f5f5;font-family:"Fira Sans",sans-serif;overflow-x:hidden}#root{z-index:60}*{box-sizing:border-box}.app__container{position:absolute;min-height:100vh;min-height:100%;width:800vw;display:flex;flex-direction:row;-webkit-transform:translateX(0px);transform:translateX(0px);transition:-webkit-transform .3s ease-in-out;transition:transform .3s ease-in-out;transition:transform .3s ease-in-out, -webkit-transform .3s ease-in-out}
.home__container{min-width:100vw;width:100vw;margin-top:130px;display:flex;align-items:center;flex-direction:column}.hero__text{color:#000;font-size:31px;text-align:center;font-weight:600;margin-bottom:50px;padding:0px 10px}.invite__text{color:#303030;font-size:24px;text-align:center;font-weight:500;margin:0}.invite__text:nth-child(3){margin-top:70px}.preview__container{position:absolute;width:100vw;height:300vh;overflow:hidden;display:none;padding-top:20px;background-color:#f5f5f5;justify-content:center}.preview__container.visible{display:flex}.preview__document{border-radius:10px}.preview__page canvas{box-shadow:0px 0px 15px #d1d1d1;border-radius:10px;width:222px}.preview__page div{border-radius:10px}
.templates__container{width:100vw;height:100%;display:flex;align-items:center;flex-direction:column}.templates__img{height:400px;box-shadow:0px 0px 12px #cfcfcf;border-radius:7px;transition:all .2s ease-out;margin-top:30px;cursor:pointer;width:290px}@media(min-width: 600px){.templates__img{width:500px;height:700px}}.templates__img:hover{-webkit-transform:scale(1.02);transform:scale(1.02);-webkit-filter:drop-shadow(0px 0px 12px #cfcfcf);filter:drop-shadow(0px 0px 12px #cfcfcf)}.templates__colors{display:flex;margin-bottom:30px;margin-top:10px;background-color:#fff;padding:20px;justify-content:center;align-items:center;box-shadow:0px 0px 12px #cfcfcf;border-radius:7px;width:78%;min-height:75px}@media(min-width: 600px){.templates__colors{width:500px}}.templates__color{width:25px;height:25px;border-radius:100%;margin:0px 10px;cursor:pointer;transition:all .2s ease-out;transition-property:width,height}.templates__color:hover{width:35px;height:35px}.templates__color.selected{border:3px solid #000;width:35px;height:35px}.templates__title{font-weight:700;text-align:center;width:100vw;font-size:31px}#gray{background-color:#e6e6e6}#green{background-color:#90c999}#blue{background-color:#91bdc7}#yellow{background-color:#f1f37b}#brown{background-color:#ad8e7d}
.dot{display:block;background-color:#fff;border-radius:100%;border:2px solid #2b3a67;width:20px;height:20px;margin:0px 5px}.dot.filled{background-color:#2b3a67}.dots{display:flex;flex-direction:row;margin-top:30px}
.button{width:140px;height:50px;color:#fff;background-color:#2b3a67;border:0;border-radius:5px;font-family:"Fira Sans";transition:all .2s ease-in-out;font-size:20px;cursor:pointer}.button:hover{background-color:#16203d}.button.button__preview{width:100px;height:60px;margin:25px 35px;font-size:17px;background-color:#f5f5f5;cursor:pointer;z-index:100;color:#2b3a67;border:2px solid #2b3a67;font-weight:bold}@media(max-width: 500px){.button.button__preview{width:70px;height:60px;font-size:14px;margin:25px 20px}}.button.button__preview:hover{background-color:#d3d3d3}.button.cancel{background-color:#ee5353;transition:all .2s ease-in-out}.button.cancel:hover{background-color:#cf3c3c}
.personal__container{display:flex;flex-direction:column;align-items:center}.personal__title{font-weight:700;text-align:center;width:100vw;font-size:31px}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type=number]{-moz-appearance:textfield}.form{width:85%;display:flex;flex-direction:column}@media(min-width: 700px){.form{width:30vw}}.form__input{height:45px;min-height:45px;padding:0px 15px;outline:none;border:2px solid #b7b7b7;border-radius:5px;background-color:#fff}.form__input-error{color:#fa1212}.form__input-error.hidden{display:none}.form__input.hidden{display:none}.form__input.error{border:2px solid #fa1212}.form__label{margin:2px 0px;margin-top:15px;-webkit-user-select:none;-ms-user-select:none;-o-user-select:none;user-select:none}.form__controls{margin:60px 0px;display:flex;justify-content:space-between;width:85%}@media(min-width: 700px){.form__controls{width:40vw}}.photo__input{cursor:pointer;display:block;background-color:#c4c4c4;color:#3e3e3e;font-weight:500;text-align:center;border-radius:5px;width:100%;height:80px;padding:30px 0px;font-size:20px;-webkit-user-select:none;-ms-user-select:none;-o-user-select:none;user-select:none}.photo__name{text-align:center;color:#049e04}
.education__container{width:100vw;display:flex;flex-direction:column;align-items:center}.education__title{font-weight:700;text-align:center;width:100vw;font-size:31px}.education__modal{z-index:10;width:100vw;position:fixed;height:100%;background-color:#f5f5f5;display:flex;justify-content:center;padding:50px 0px}.education__modal.hidden{display:none}.education__modal .form__controls{justify-content:space-between;width:100%}.plus__button{background-color:#c4c4c4;width:55px;height:55px;border-radius:35px;cursor:pointer}.plus__button::before{content:"";display:block;height:6px;background-color:#fff;width:25px;-webkit-transform:translate(15px, 24px);transform:translate(15px, 24px)}.plus__button::after{content:"";display:block;height:6px;background-color:#fff;width:25px;-webkit-transform:translate(15px, 18px) rotate(90deg);transform:translate(15px, 18px) rotate(90deg)}.school{width:85%;min-height:60px;border:2px solid #b7b7b7;border-radius:5px;padding:10px;background-color:#fff;margin-bottom:10px;cursor:pointer}@media(min-width: 700px){.school{width:40vw}}.school:hover{-webkit-filter:brightness(0.4);filter:brightness(0.4)}.school:hover::after{content:"DELETE";position:absolute;color:#383838;font-size:35px;text-align:center;width:100%;font-weight:bold;left:0;top:25%}.school__major{font-weight:600;margin:0;margin-bottom:2px}.school__name{margin:0}.school__years{display:flex;flex-direction:row;justify-content:space-between}.school__years .form__input{width:45%}.school__years-info{margin:0;margin-bottom:3px}
.work__start select:nth-child(2){margin-left:25px}.work__end select:nth-child(2){margin-left:25px}.work__container{display:flex;flex-direction:column;align-items:center}.work__title{font-weight:700;text-align:center;width:100vw;font-size:31px}.work__modal{z-index:10;width:100vw;position:fixed;height:100vh;background-color:#f5f5f5;display:flex;justify-content:center;padding:50px 0px}.work__modal.hidden{display:none}.work__modal .form__controls{justify-content:space-between;width:100%}
.languages__container{display:flex;flex-direction:column;align-items:center}.languages__title{font-weight:700;text-align:center;width:100vw;font-size:31px}.languages__modal{z-index:10;width:100vw;position:fixed;height:100vh;background-color:#f5f5f5;display:flex;justify-content:center;padding:50px 0px}.languages__modal.hidden{display:none}.languages__modal .form__controls{justify-content:space-between;width:100%}.language__info{display:flex;flex-direction:row;width:85%;background-color:#fff;border:2px solid #b7b7b7;border-radius:5px;justify-content:space-between;padding:10px;margin-bottom:20px}@media(min-width: 700px){.language__info{width:40vw}}.language__name{font-weight:600;text-align:left;margin:0}.language__level{text-align:right;margin:0}
.skills__container{display:flex;flex-direction:column;align-items:center}.skills__title{font-weight:700;text-align:center;width:100vw;font-size:31px}.skills__modal{z-index:10;width:100vw;position:fixed;height:100vh;background-color:#f5f5f5;display:flex;justify-content:center;padding:50px 0px}.skills__modal.hidden{display:none}.skills__modal .form__controls{justify-content:space-between;width:100%}.skill__info{display:flex;flex-direction:row;width:85%;background-color:#fff;border:2px solid #b7b7b7;border-radius:5px;justify-content:space-between;padding:10px;margin-bottom:20px}@media(min-width: 700px){.skill__info{width:40vw}}.skill__name{font-weight:600;text-align:left;margin:0}
.download__container{margin-top:140px;display:flex;flex-direction:column;align-items:center}.download__container button{margin:50px 0px}.download__container a{display:block;text-decoration:none;color:#fff;height:100%;width:100%;text-align:center;line-height:48px}.download__title{font-weight:700;text-align:center;width:100vw;font-size:31px}
.logo{width:50vw;color:#2b3a67;font-size:35px;font-weight:800;text-align:center;position:absolute;left:25%;top:0}
